import './token-specimen';
import type { TokensStructure } from '../types';

class Tier3Button extends HTMLElement {
	private tokens: TokensStructure | null = null;

	constructor() {
		super();
	}

	connectedCallback() {
		this.loadTokens();
		this.render();

		document.addEventListener('DOMContentLoaded', () => {
			setTimeout(() => {
				this.loadTokens();
				this.render();
			}, 10);
		});
	}

	loadTokens() {
		if (window.ouiTokens) {
			this.tokens = window.ouiTokens;
		}
	}

	filterTokens() {
		if (!this.tokens || !this.tokens.tier3 || !this.tokens.tier3.button) {
			return [];
		}
		return this.tokens.tier3.button || [];
	}

	findKnockoutBackground(contentTokenName) {
		const backgroundTokenName = contentTokenName.replace('-content-', '-background-');
		const allTokens = this.filterTokens();
		const matchingBgToken = allTokens.find((token) => token.name === backgroundTokenName);

		if (matchingBgToken) {
			const bgCssVariable = matchingBgToken.value.match(/var\(([^)]+)\)/)?.[1] || matchingBgToken.cssVar;
			return getComputedStyle(document.documentElement).getPropertyValue(bgCssVariable).trim() || matchingBgToken.value;
		}

		return null;
	}

	groupTokensByType(tokens) {
		const groups = {
			background: [],
			content: [],
			border: []
		};

		tokens.forEach((token) => {
			if (token.name.includes('-background-')) {
				groups.background.push(token);
			} else if (token.name.includes('-content-')) {
				groups.content.push(token);
			} else if (token.name.includes('-border-')) {
				groups.border.push(token);
			}
		});

		return groups;
	}

	renderTokenGroup(tokens, variant = 'color') {
		if (!tokens || tokens.length === 0) {
			return '';
		}

		return tokens
			.map((token) => {
				const cssVariable = token.value.match(/var\(([^)]+)\)/)?.[1] || token.cssVar;
				const computedValue =
					getComputedStyle(document.documentElement).getPropertyValue(cssVariable).trim() || token.value;

				const isKnockout = token.name.includes('knockout');
				const knockoutBg = variant === 'content' && isKnockout ? this.findKnockoutBackground(token.name) : null;

				return `
        <token-specimen
          semantic-name="${token.name}"
          css-variable="${cssVariable}"
          value="${computedValue}"
          variant="${variant}"
          ${knockoutBg ? `knockout-background="${knockoutBg}"` : ''}
        ></token-specimen>
      `;
			})
			.join('');
	}

	render() {
		const tokens = this.filterTokens();

		if (!tokens || tokens.length === 0) {
			this.innerHTML = '<p>No button tokens available</p>';
			return;
		}

		const groups = this.groupTokensByType(tokens);

		this.innerHTML = `
      ${
				groups.background.length > 0
					? `
        <section class="section">
          <header class="section__header">
            <h2 class="section__heading">Background Colors</h2>
          </header>
          <div class="token-list">
            ${this.renderTokenGroup(groups.background, 'color')}
          </div>
        </section>
      `
					: ''
			}

      ${
				groups.content.length > 0
					? `
        <section class="section">
          <header class="section__header">
            <h2 class="section__heading">Content Colors</h2>
          </header>
          <div class="token-list">
            ${this.renderTokenGroup(groups.content, 'content')}
          </div>
        </section>
      `
					: ''
			}

      ${
				groups.border.length > 0
					? `
        <section class="section">
          <header class="section__header">
            <h2 class="section__heading">Border Colors</h2>
          </header>
          <div class="token-list">
            ${this.renderTokenGroup(groups.border, 'color')}
          </div>
        </section>
      `
					: ''
			}
    `;
	}
}

if (!customElements.get('tier-3-button')) {
	customElements.define('tier-3-button', Tier3Button);
}

export default Tier3Button;
